import { notInFuture, validarCnpj } from "../../../herpers/helpers";
import { EmpresaService, EnderecoService } from '../../../services'
import moment from 'moment'
import container from '../../../components/forms/container.vue';

const empresaService = new EmpresaService();
const enderecoService = new EnderecoService();

export default {
  components: { container },

  data() {
        return {
            form: {
                id: null,
                apelido: '',
                matriz_id: null,
                razao_social: '',
                nome_fantasia: '',
                cnpj: '',
                ie: '',
                ie_st: '',
                im: '',
                crt: '',
                cmc: '',
                cnae: '',
                incentivador_cultural: false,
                
                ativa: true,
                cidade_id: '',
                endereco_tipo: '',
                logradouro: '',
                numero: '',
                complemento: '',
                bairro: '',
                cep: '',
                fone: '',
                email: '',
                nfce_emissor: false,
                nfce_producao: false,
                nfse_emissor: false,
                nfse_producao: false,
                nfe_emissor: false,
                nfe_producao: false,
                cte_emissor: false,
                cte_producao: false,
                mdfe_emissor: false,
                mdfe_producao: false,
                mde_emissor: false,
                mde_producao: false,
                fiscal_producao: false,
                certificado_digital: '',
                certificado_digital_senha: '',
                certificado_digital_vencimento: '',                
            },

            certificado_digital:{
                certificado_digital: '',
                certificado_digital_senha: '',
            },
          
            sucesso: null,
            erros:null,
            errosTitulo:'',
              
            timer: null,
            cidades: [],
            cidade:{},
            buscandoCidade: false,


            eFilial: false,
            matriz: null,
            buscandoMatrizes: false,
            matrizes: [],

            cnaes: [],

            cnaeModal: null,
            servicoModal: null,

            errosModal: null,
            sucessoModal: null,

            sucessoCertificado: null,
            errosCertificado: null,
            sucessoHabilitaFiscal: null,
            errosHabilitaFiscal: null,

        } ;
    },
    validations: {
        form: {
            apelido: { required, maxLength:maxLength(50)},
            razao_social: { required, maxLength:maxLength(200)},
            nome_fantasia: { required, maxLength:maxLength(200)},
            cnpj: { required, maxLength:maxLength(20), validarCnpj:validarCnpj},
            ie: { maxLength:maxLength(20)},
            ie_st: { maxLength:maxLength(20)},
            im: { maxLength:maxLength(20)},
            crt: { required, maxLength:maxLength(20)},
            cmc: { maxLength:maxLength(20)},
            // cnae: { required, maxLength:maxLength(20)},
            incentivador_cultural: { required },
            ativa: { required },
            cidade_id: { required},
            endereco_tipo: { required, maxLength:maxLength(20)},
            logradouro: { required, maxLength:maxLength(100)},
            numero: { required, maxLength:maxLength(10)},
            complemento: { maxLength:maxLength(100)},
            bairro: { required, maxLength:maxLength(100)},
            cep: { required, maxLength:maxLength(10)},
            fone: { required, maxLength:maxLength(20)},
            email: { required, email, maxLength:maxLength(150)},
            // nfce_emissor: { },
            // nfce_producao: { },
            // nfse_emissor: { },
            // nfse_producao: { },
            // nfe_emissor: { },
            // nfe_producao: { ',      } 
            // cte_emissor: { },
            // cte_producao: { },
            // mdfe_emissor: { },
            // mdfe_producao: { },
            // mde_emissor: { },
            // mde_producao: { },
            // certificado_digital: { },
            // certificado_digital_senha: { maxLength:maxLength(100)},
            matriz_id: { required: requiredIf( function() { return this.eFilial; } )}
            // certificado_digital_vencimento: { },
            // extras: { nullable } 
        },

        cnaeModal: {
            empresa_id: { required},
            codigo: { required, maxLength:maxLength(10)},
            descricao: { required, maxLength:maxLength(200)},            
        },

        servicoModal: {
            // empresa_id: { required},
            cnae_id: { required},
            codigo: { required, maxLength:maxLength(10)},
            descricao: { required, maxLength:maxLength(200)},            
        }

    },
    mounted() {
        if (this.$route.params.id) {
            this.buscarObjeto(this.$route.params.id);
        }       
    },
    methods: {

        submit: function(){

            this.$v.form.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulrio corretamente, alguns campos esto com erros!'
                this.erros = this.$v.form;
                return false;
            }
            let obj = {...this.form};
            let request = null;
            if(obj.id)    
                request = empresaService.atualizar(obj);
            else
                request = empresaService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    this.form = response.data;
                    this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => this.erros = erro )

        },

        buscarObjeto: function(id){
            empresaService.buscar(id)
                .then(  response => {
                    this.form = response.data;
                    this.roles = response.data.roles;
                    this.form.data_nascimento = this.form.data_nascimento ? moment(this.form.data_nascimento).format("DD/MM/YYYY") : "";
                    this.cidade = { id: response.data.cidade.id, nome: response.data.cidade.nome, sigla: response.data.cidade.estado.sigla };
                    this.matriz = response.data.matriz;

                    this.cnaes = response.data.cnaes.map(x => {
                        x.ver_servicos = false;
                        return x;
                    });
                }).catch( erro => this.erros = erro)
        },

        buscarCep: function(){
            enderecoService.buscarEnderecoPorCep(this.form.cep)
                .then((response) => {
                
                if(response.data.logradouro){
                    this.form.logradouro = response.data.logradouro.substr( response.data.logradouro.indexOf(' ')+1 );
                    this.form.endereco_tipo = response.data.logradouro.substr(0, response.data.logradouro.indexOf(' '));
                }else{
                    this.form.logradouro = '';
                    this.form.endereco_tipo = '';
                }
                this.form.bairro = response.data.bairro;
                enderecoService.buscarCidadePorIbge(response.data.ibge)
                    .then((responseCidade) => {
                        console.log(responseCidade.data);
                        this.cidade = { id: responseCidade.data.id, nome:responseCidade.data.nome ,sigla: responseCidade.data.estado.sigla };
                        this.form.cidade_id = this.cidade.id;
                        this.cidades = [];
                    })
                    .catch((errorCity) => {
                        console.log(errorCity);
                    });
                })
                .catch((error) => {});
        },

        buscarCidadesDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarCidades(text, loading)
            }, 700);
        },

        buscarCidades: function(text, loading){
            enderecoService.listarCidadesBusca({texto: text})
                .then( response =>  this.cidades = response.data.data )
                .catch( error => console.log(error))
        },

        buscarMatrizesDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarMatrizes(text, loading)
            }, 700);
        },

        buscarMatrizes: function(text, loading){
            this.buscandoMatrizes = true;
            empresaService.listar({texto: text, per_page: 50, id: this.form.id, matrizes: 1})
                .then( response =>  this.matrizes = response.data.data )
                .catch( error => console.log(error))
                .finally( () => this.buscandoMatrizes = false)
        },


        abrirModalCnae: function(cnae){
            this.sucessoModal = null;
            this.errosModal = null;
            this.$v.cnaeModal.$reset()
            if(cnae)
                this.cnaeModal = {...cnae}
            else
                this.cnaeModal = { empresa_id: this.form.id, ativo: true}
        },

        salvarCnae: function(){
            this.$v.cnaeModal.$touch();
            this.errosModal = null;
            this.sucessoModal = null;    

            if (this.$v.cnaeModal.$invalid){
                this.errosModal = this.$v.cnaeModal;
                return false;
            }
            let obj = {...this.cnaeModal};
            let request = null;
            request = empresaService.salvarCnae(obj);

            request.then(
                response => {

                    if(this.cnaeModal.id){
                        this.cnaes.splice(this.cnaes.map(x => x.id).indexOf(this.cnaeModal.id), 1, response.data)
                    }else{
                        this.cnaes.push(response.data);
                    }

                    this.cnaeModal = response.data;
                    this.sucessoModal = "Registro salvo com sucesso!";                   
                }   
            ).catch( erro => this.errosModal = erro )
        },

        inativarCnae: function(){
            this.errosModal = null;
            this.sucessoModal = null;
           
            let obj = {...this.cnaeModal};
            obj.ativo = false;
            let request = null;
            request = empresaService.salvarCnae(obj);

            request.then(
                response => {

                    if(this.cnaeModal.id){
                        this.cnaes.splice(this.cnaes.map(x => x.id).indexOf(this.cnaeModal.id), 1, response.data)
                    }else{
                        this.cnaes.push(response.data);
                    }

                    this.cnaeModal = response.data;
                    this.sucessoModal = `Cnae ${this.cnaeModal.codigo} inativado sucesso!`;                   
                }   
            ).catch( erro => this.errosModal = erro )
        },

        abrirServicos: function(cnae){
            console.log(cnae);
            cnae.ver_servicos = !cnae.ver_servicos
            this.$forceUpdate();
        },

        abrirModalServico: function(cnae, servico){
            this.sucessoModal = null;
            this.errosModal = null;
            this.$v.servicoModal.$reset()
            if(servico)
                this.servicoModal = {...servico}
            else
                this.servicoModal = { empresa_id: this.form.id, cnae_id: cnae.id, ativo: true}

            this.cnaeModal = cnae;
        },

        salvarServico: function(){
            this.$v.servicoModal.$touch();
            this.errosModal = null;
            this.sucessoModal = null;    

            if (this.$v.servicoModal.$invalid){
                this.errosModal = this.$v.servicoModal;
                return false;
            }
            let obj = {...this.servicoModal};
            let request = null;
            request = empresaService.salvarServico(obj);

            request.then(
                response => {

                    if(this.servicoModal.id){
                        this.cnaeModal.servicos.splice(this.cnaeModal.servicos.map(x => x.id).indexOf(this.servicoModal.id), 1, response.data)
                    }else{
                        this.cnaeModal.servicos.push(response.data);
                    }

                    this.servicoModal = response.data;
                    this.sucessoModal = "Registro salvo com sucesso!";                   
                }   
            ).catch( erro => this.errosModal = erro )
        },

        inativarServico: function(){
            this.errosModal = null;
            this.sucessoModal = null;
           
            let obj = {...this.servicoModal};
            obj.ativo = false;
            let request = null;
            request = empresaService.salvarServico(obj);

            request.then(
                response => {

                     if(this.servicoModal.id){
                        this.cnaeModal.servicos.splice(this.cnaeModal.servicos.map(x => x.id).indexOf(this.servicoModal.id), 1, response.data)
                    }else{
                        this.cnaeModal.servicos.push(response.data);
                    }

                    this.servicoModal = response.data;
                    this.sucessoModal = `Servio ${this.servicoModal.codigo} inativado sucesso!`;                   
                }   
            ).catch( erro => this.errosModal = erro )
        },


        setCertificado: function(event){
                event.preventDefault();
                this.certificado_digital.certificado_digital = event.target.files[0];
        },

        atualizarCertificado: function(){
            
            this.sucessoCertificado = null;
            this.errosCertificado = null;

            let formdata = new FormData();
            formdata.append('certificado_digital', this.certificado_digital.certificado_digital);
            formdata.append('certificado_digital_senha', this.certificado_digital.certificado_digital_senha);
            
            empresaService.atualizarCertificado(formdata, this.form.id)
                .then((response) => {                
                    this.sucessoCertificado = "Certificado atualizado com sucesso!";
                    this.form.certificado_digital = response.data.certificado_digital;
                    this.form.certificado_digital_vencimento = response.data.certificado_digital_vencimento;
                    this.certificado_digital =  {};
                })
                .catch((response) => {
                    this.errosCertificado = response;
                });
            
        },

        habilitarFiscal: function(ambiente){
            this.sucessoHabilitaFiscal = null;
            this.errosHabilitaFiscal = null;

            
            
            empresaService.habilitarFiscal(this.form.id, ambiente)
                .then((response) => {                
                    this.sucessoHabilitaFiscal = "Empresa atualizado e habilitada com sucesso!";
                    // this.form.
                })
                .catch((response) => {
                    this.errosHabilitaFiscal = response;
                });

        }
    },
    computed:{
        certificado_digital_vencido(){
            if (!this.form.certificado_digital_vencimento)
                return false;

            let diferenca = moment(this.form.certificado_digital_vencimento).diff(moment());
            return Math.round(moment.duration(diferenca) / 60000) < 0;

        }
    }
};
</script>
